import React, { useReducer, useEffect } from "react";
import "./Input.css";
import { validate } from "../../util/validators";

// Reducer
const inputReducer = (state, action) => {
  switch (action.type) {
    case "CHANGE":
      return {
        ...state,
        inputValue: action.payload,
        isValid: validate(action.payload, action.validators),
      };
    case "TOUCH":
      return {
        ...state,
        isTouched: true,
      };
    default:
      return state;
  }
};

const Input = (props) => {
  // State
  const [inputState, dispatch] = useReducer(inputReducer, {
    inputValue: props.initialValue || "",
    isValid: props.initialIsValid || false,
    isTouched: false,
  });

  const { id, onInput } = props;
  const { inputValue, isValid } = inputState;

  useEffect(() => {
    onInput(id, inputValue, isValid);
  }, [id, inputValue, onInput, isValid]);

  // Передача введенных символов в поля, и их валидация
  const onChangeHandler = (event) => {
    dispatch({ type: "CHANGE", payload: event.target.value, validators: props.validators });
  };

  const touchHandler = () => {
    dispatch({ type: "TOUCH" });
  };

  const element =
    props.element === "input" ? (
      <input
        id={props.id}
        type={props.type}
        placeholder={props.placeholder}
        value={inputState.value}
        onChange={onChangeHandler}
        onBlur={touchHandler}
      />
    ) : (
      <textarea
        id={props.id}
        rows={props.rows || 3}
        value={inputState.value}
        onChange={onChangeHandler}
        onBlur={touchHandler}
      />
    );
  return (
    <div className={`form-control ${!inputState.isValid && inputState.isTouched && "form-control--invalid"}`}>
      <label htmlFor={props.id}>{props.label}</label>
      {element}
      {!inputState.isValid && inputState.isTouched && <p>{props.errorText}</p>}
    </div>
  );
};

export default Input;
